import React from 'react';

class TodoActionSection extends React.Component {
	constructor(props) {
        super(props);
    }
   render() {
   	   return (
            <td>
                <button onClick={this.props.deleteTask.bind(this, this.props.task)}>Delete</button>
            </td>
        );
   }
}

export default TodoActionSection;


